@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600&display=swap);
.movie-stats-main-container {
    background-color: wheat;
    border-radius: 10px;
    padding: 50px;
    margin-top: 10px;
    text-align: center;
}
.movie-form-main-container {
    background-color: wheat;
    border-radius: 10px;
    padding: 50px;
}

.movie-form-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.btn {
    background-color:rgb(160, 192, 192);
    color:rgb(123, 138, 138)
}

.btn:hover {
    background-color:rgb(172, 158, 158);
    color: whitesmoke;
    cursor: pointer;
}

.movie-form-container input {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid palegreen;
    border-radius: 5px;
}
.movie-list-container {
    width: 800px;
}

.form-control-movieList {
    display: -webkit-flex;
    display: flex;
}

.form-control-movielist input {
    width: 80%;
    margin-right: 5px;
    padding: 5px;
    border: 1px solid whitesmoke;
    border-radius: 5px;
    margin-bottom: 10px;
}

.form-control-movieList select {
    width: 20%;
    padding: 5px;
    border: 1px solid white;
    border-radius: 5px;
    margin-bottom: 10px;
}

.form-control-movieList input:focus,
.form-control-movieList select:focus {
    outline: transparent;
}
.movie-card-main-flex{
    display: -webkit-inline-flex;
    display: inline-flex;
    margin: 10px;
    background-color: whitesmoke;
    padding: 10px;
    border-radius: 10px;
    margin-left: 30px;
}

.movie-card-item{
    text-align: center;
    padding: 10px;
}

.btn-delete{
    border: transparent;
    cursor: pointer;
    background-color: transparent;
}
.movie-container-top-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.aside-container {
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column;
    -webkit-align-items:flex-end;
            align-items:flex-end;
}
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    background-color: #e2a52327;
}

